.single {
    #content {
        margin-bottom: 0;
    }
}

.header-single{
    display: flex;
    align-items: center;
    background-size: cover;
    position: relative;
    filter: grayscale(10%);

    &:before{
        top: 0;
        width: 100%;
        z-index: -1;
        height: 100%;
        opacity: 0.75;
        position: absolute;
    }

    .row{ margin: 0 auto; width: 100%; }

    h1, p{
        margin: 0;
        padding: 0;
        color: white;
        font-size: 17px;
        font-weight: 700;
        font-family: 'Raleway';

        &.raleway{ font-weight: 400; }
    }

    .pessoas{
        line-height: 1;
        font-size: 20px;
        margin-top: 10px;
        a {
            color: #fff;
            &:hover {
                text-decoration: underline;
            }
        }
    }

    .box-title { font-size: 14px; }

    .box{
        font-size: 14px;
        padding: 0px 5px;
        display: inline-block;
        text-transform: lowercase;
    }

    h1{
        font-size: 50px;
        font-weight: 900;
        line-height: 60px;
    }

    .line {
        background-color: white !important;
        width: 60%;
        margin: 10px 0px;
    }
}

.row.single-menu-container{
    width: 100%;
    display: flex;
}

.header-single{
    height: 300px;
    background-color: purple;

    @media #{$small-only}{
        height: auto;
        padding: 40px 20px;
    }

    &-menu{
        width: 100%;
        height: 40px;
        color: black;
        font-weight: bold;

        &.fixed + &-fix{
            width: 100%;
            height: 40px;
        }

        &.fixed{
            position: fixed;
            top: 75px;
            box-shadow: 0 4px 7px -5px black;
        }
    }

}

@media #{$medium-up}{
    .single-menu{
        flex: 1;
        ul{
            display: flex;
            list-style-type: none;
        }
// margin: 0 50px 0 0;
// justify-content: space-between;

li{
    display: block;
    padding: 0px 30px;
    font-weight: initial;

    a {
        color: white;
        &.active{
            font-weight: bold;
            padding-bottom: 4px;
            border-bottom: 5px inset white;
        }
    }

    &:first-child{ padding-left: 10px; }
    &:last-child{ border-right: none; }
}
}
}

.sidebar{
    width: 320px;
    display: flex;
    align-items: center;
    justify-content: flex-end;

    @media #{$small-only}{
        display: none;
    }
}

.tab {
    display: flex;
    overflow: hidden;
    align-items: baseline;

    &-title{
        width: 200px;
        margin-right: 30px;
    }

    &-content{
        width: calc(100% - 380px);

        .list-title{
            display: block;
            margin: 0 0 15px 0 !important;
        }
    }
}

.content-area{
    padding: 50px 40px 60px;
    @media #{$small-only}{
        padding: 40px 20px !important;
    }

// &:nth-of-type(even){// 	background-color: #D9D9D9 !important; // }

.row{
    width: 100% !important;
    margin: 0 auto !important;
}

&.tab .row{ display: flex; }
