/********** COMPONENTES **************/

/* COMPONENTE PALESTRANTES */
.content-publicacoes{
    img{
        float: left;
        margin-right: 50px;
        box-shadow: 0px 0px 5px gray;
        max-width: 300px;
    }

    p, h3{
        max-width: 580px;
        font-family: 'Raleway';
    }

    h3 { margin-bottom: 15px; }

    p{
        font-size: 14px;
        line-height: 20px;
    }

    .publicacao { margin-bottom: 50px; overflow: hidden; }
}

/* COMPONENTE PALESTRANTES */
.pessoa {
    float: left;
    position: relative;
    margin: 0 25px 25px 0;
    height: 135px;
    width: 100px;

    input[type="radio"] {
        display: none;
    }

    .pessoa-mini {
        .img {
            background-size: cover;
            background-position: center;
            width: 85px;
            filter: gray;
            opacity: 0.7;
            height: 85px;
            border-radius: 50%;
            filter: grayscale(100%);
            -webkit-filter: grayscale(1);
            -webkit-filter: grayscale(100%);
            cursor: pointer;

            &:hover {
                filter: grayscale(0%);
            }

            &:img {
                opacity: 1;
                filter: grayscale(0%);
            }
        }
        .name {
            font-size: 14px;
            font-weight: 600;
            margin: 5px 0 0;
            opacity: 0.75;
        }
        .line {
            border-radius: 5px;
            height: 5px;
            opacity: 0;
        }
    }

    input[type="radio"]:checked {
        & + .pessoa-mini {
            .name {
                font-weight: 700;
                opacity: 1;
            }
            .line {
                opacity: 1;
            }
        }
        & + .pessoa-mini .img {
            opacity: 1;
            display: block;
            filter: grayscale(0%);
        }
    }

    &:hover {
        .name {
            font-weight: 700;
            opacity: 1;
        }
    }
}

.pessoa-info {
    display: flex;
    justify-content: center;
    width: calc(100% + 80px);
    margin-left: -40px;
    color: white;
    height: auto;
    max-height: 0px;
    font-size: 14px;
    overflow: hidden;
    font-family: 'Raleway';
    background-color: #000;
    transition: max-height .3s;

    .close {
        display: none;
    }

    .pessoa-info-content {
        float: right;
        margin: 20px 0;
        position: relative;

        .pessoa-thumb {
            box-sizing: border-box; 
            float: left;
            margin: 0 35px 0 0; 
            padding: 10px;

            .img {
                background-size: cover;
                background-position: center;
                border: 0;
                border-radius: 50%;
                height: 160px;
                width: 160px;
                outline: none;
            }
        }

        .pessoa-text {
            float: left;
            max-width: 480px;
            h3 {
                margin-bottom: 5px;
            }
        }
    }
    &.active {
        max-height: 440px;

        .pessoa-info-content {
            .pessoa-thumb {
                .img {
                    box-shadow: 0 0 0 3px #fff, 0 0 0 7px #999, 0 0 0 11px #444;
                }
            }
            .close {
                display: block;
                right: -70px;
                margin-top: 0;
            }
        }
    }
}

.related-content {
    background-color: #d9d9d9;
    .large-4{

        &:nth-child(3n + 1){
            padding-left: 0px !important;
        }

        @media #{$small-only}{
            padding: 0px;
        }
    }
}


/* COMPONENT AULAS E COMPONENTS*/
.component-tabs{
    .tab-content { 
        padding: 0 20px; overflow: hidden;
        position: relative;

        @media #{$small-only}{
            width: 100%;
            padding: 0px !important;
        }
    }

    .columns:nth-of-type(even){
        padding-right: 0px;
    }

    .columns:nth-of-type(odd){
        padding-left: 0px;
    }

    .component-tabs-title{
        width:200px;
        float: left;

        @media #{$small-only}{
            display: none !important;
        }

        ul {
            display: block;
            li{
                font-size: 15px;
                font-family: 'Raleway';
            }
        }

        h5 {
            font-size: 15px;
            margin: 10px 0px !important;
            &:not(.label) {
                border-bottom: 1px solid black !important;
            }
        }
    }

    .component-tabs-tab{
        position: relative;
        margin-bottom: 50px;

        &:last-child{
            margin-bottom: 0;
        }

        h1{
            display: block;
            position: relative;
            margin: 0 !important;
        }

        .tab-subtitle {
            display: flex;
            justify-content: space-between;
            .right{
                height: 100%;
                margin-left: 20px;
                white-space: nowrap;
            }
        }

        p.right{
            font-size: 12px;
            border-bottom: 1px solid black;
        }

        p.left{
            font-size: 18px;
        }

        .component-tabs-content, p.left{
            font-family: 'Raleway';
        }

        .component-tabs-content{
            width: 100%;
            font-size: 15px;
            overflow: hidden;
            max-height: 620px;
            transition: all .3s;

            ul{
                display: flex;
                flex-direction: column;

                li{
                    display: inline-table;
                    padding: 5px;
                    margin-bottom: 5px;
                    background-color: #d9d9d9;
                }
            }
        }
    }
}


.component-tabs.aulas {
    label.label-tab {
        top: 10px;
    }
    .list-title {
        text-transform: lowercase; 
    }
    p.left {
        max-width: 410px;
    }
    p.left, p.right {
        font-weight: bold;
    }
    .component-tabs-content {
        font-weight: 600;
    }
}

.component-tabs.informacao {
    .tab-content {
        width: calc(100% - 270px);

        .list-title {
            width: calc(100% - 90px);
        }
        label.label-tab {
            right: 110px;
        }
    }
}

.component-tabs.equipe {
    .tab-content {
        .component-tabs-content {
            margin: 30px 0 30px 30px; 
        }
        .pessoa {
            .pessoa-mini {
                .line {
                    background-color: #522e91;
                }
            }
            input[type="radio"]:checked {
                & + .pessoa-mini {
                    .name {
                        color: #522e91;
                    }
                }
            }
        }
    }
    .pessoa-info {
        background-color: #522e91;
        &.active {
            .pessoa-thumb {
                .img {
                    box-shadow: 0 0 0 3px #fff, 0 0 0 7px #bfb1d6, 0 0 0 11px #7e63ad;
                }
            }
        }
    }
}

