//COMPONENT MAPA
.component-tabs.map {
    > .row {
        padding-bottom: 25px;
        position: relative;
    }
    .tab-content { 
        width: calc(100% - 240px);
    }
}

.markers {
    position: absolute;
    width:20px;
    cursor: pointer;
    transition: all .3s;
    border-radius: 50%;

    &.selected {
        padding: 4px;
        border: 3px solid #4e0a03;
        background-color: #7d0c00;
    }
}
#map-editor {
    position: relative;
}

#mapa {
    margin: 0 auto; 
    display: block;
    width: 100%;
}
.map-info-carousel {
    background-color: #9a258f;
    overflow: inherit;
    position: relative;
    .seta-mapa {
        display: none;
        position: absolute;
        top: -15px;
        width: 0;
        height: 0;
        border-style: solid;
        border-width: 0 15px 15px 15px;
        border-color: transparent transparent #9a258f transparent;
    }
    .carousel-cell {
        height: auto;
        width: auto;
    }
    .map-info-content {
        position: relative;
        .close {
            top: 20px;
        }
    }
    .map-thumb {
        float: left;
        margin-right: 35px;
        position: relative;
        .color-hover {
            background-color: #9a258f;
            position: absolute;
            top: 0;
            right: 0;
            bottom: 0;
            left: 0;
            opacity: 0.5;
        }
        img {
            height: auto;
            max-width: 400px;
        }
    }
    .map-text {
        float: left;
        margin: 30px 0; 
        max-width: 530px;
        h3 {
            margin-bottom: 5px;
        }
    }
    &.active {
        .seta-mapa {
            display: block;
        }
        .map-info-content .close {
            display: block;
            right: -70px;
            margin-top: 0;
        }
    }
}

